.flex{ 
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.item{
    padding-right: 5%;
    width:210px;
    padding-bottom: 20px;
}

.item p{
    min-height: 60px;
}
.flex img{
    width: 210px;
    height: 260px;
}

.Icon--share_24{
    float:left;
    padding-right: 5px;
}

.button_share{
    font-size: 17px;
    font-weight: 500;
    color: #fff; /* цвет текста */
    text-decoration: none; /* убирать подчёркивание у ссылок */
    user-select: none; /* убирать выделение текста */
    background: #33bcb5;
    padding: .7em 1.5em; /* отступ от текста */
    outline: none; /* убирать контур в Mozilla */
    border:0px;
    cursor: pointer;
    position: relative;
    bottom: 10px;
}


.infoLastName{
    text-align: center;
}

.button_share:hover { background: #37948f; } /* при наведении курсора мышки */
.button_share:active { background: #0d3b39; } /* при нажатии */